*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --color-violet: #4f2ee8;
  --color-yellow: #eac645;
  --font-color-white: #fff;
  --font-color-grey: #6c6c6c;
  --font-color-violet: #4f2ee8;
  --font-color-yellow: #eac645;
}

body.light {
  --background-color: #fff;
  --background-color1: #f6f6f6;
  --font-color: #111;
  --background-checkbox-theme: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --border-color: #202024;
  --background-color-submit: #111;
  --text-color-submit: #f3f3f3;
  --text-buttons-submit: #11111180;
  --text-color-auth-form: #111;
  --background-color-auth-form: #f6f6f6;
  --background-color-submit-active: #333;
  --color-text-category: #1119;
  --color-text-categor-hover: #4f2ee8;
  --color-line-scroll: #e0e0e0;
  --background-color1: #f6f6f6;
  --font-color: #111;
  --background-checkbox-theme: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --color-pagination-fill: #b4afaf;
}

body.dark {
  --background-color: #000;
  --background-color1: #202024;
  --font-color: #fff;
  --background-checkbox-theme: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  --border-color: #f6f6f6;
  --background-color-submit: #f6f6f6;
  --text-color-submit: #202024;
  --text-buttons-submit: #f6f6f680;
  --text-color-auth-form: #f6f6f6;
  --background-color-auth-form: #202024;
  --background-color-submit-active: #f6f6f6;
  --color-text-category: #fff9;
  --color-text-categor-hover: #eac645;
  --color-line-scroll: #1119;
  --background-color1: #202024;
  --font-color: #fff;
  --background-checkbox-theme: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  --color-pagination-fill: #fff;
}

@font-face {
  font-family: DM Sans Regular;
  src: url("DMSans-Regular.dac2326d.woff") format("woff"), url("DMSans-Regular.edc7ffdd.ttf") format("truetype");
  font-weight: 400;
  ont-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans Italic;
  src: url("DMSans-Italic.e947e2a0.woff") format("woff"), url("DMSans-Italic.69cf9cd1.ttf") format("truetype");
  font-weight: 400;
  ont-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans Medium;
  src: url("DMSans-Medium.ea7c3d0c.woff") format("woff"), url("DMSans-Medium.4c545a04.ttf") format("truetype");
  font-weight: 500;
  ont-display: swap;
  font-style: normal;
}

@font-face {
  font-family: DM Sans Bold;
  src: url("DMSans-Bold.c55f0cba.woff") format("woff"), url("DMSans-Bold.faef4562.ttf") format("truetype");
  font-weight: 700;
  ont-display: swap;
  font-style: normal;
}

body {
  background-color: var(--background-color);
  color: var(--font-color);
  font-family: DM Sans Regular, sans-serif;
}

.container {
  width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding: 0 24px;
  }
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  width: 100%;
  position: relative;
}

.header-top {
  justify-content: space-between;
  display: flex;
}

.header-container {
  z-index: 1;
  background-color: var(--background-color);
  border: 1.5px solid var(--font-color);
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
  position: relative;
}

.logo-header {
  align-items: center;
  gap: 8px;
  display: flex;
}

.logo-bookshelf {
  fill: var(--font-color);
}

.block-btn {
  justify-content: end;
  align-items: center;
  gap: 14px;
  display: flex;
}

.menu-btn {
  background-color: #0000;
  border: none;
  margin-left: auto;
  padding: 0;
  line-height: 0;
  display: inline-flex;
}

.menu-btn .open-menu, .menu-btn .close-menu {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.open-menu {
  stroke: var(--font-color);
}

.menu-btn .open-menu:hover, .menu-btn .open-menu:focus {
  stroke: var(--font-color-violet);
  transform: scale(.95);
}

.close-menu {
  stroke: var(--font-color);
}

.menu-btn .close-menu:hover, .menu-btn .close-menu:focus {
  stroke: var(--font-color-violet);
  transform: scale(.95);
}

.btn-singup, .header-nav {
  display: none;
}

.mobile-menu {
  z-index: 0;
  width: 100%;
  height: 100vh;
  background-image: radial-gradient(189.32% 190.93% at 59.76% -23.42%, var(--color-violet) 18.03%, #fff 100%);
  flex-direction: column;
  padding-top: 84px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.mob-btn-singup {
  width: 133px;
  height: 37px;
  letter-spacing: -.02em;
  color: var(--font-color-white);
  background-color: #0000;
  border: #0000;
  align-items: center;
  gap: 9px;
  margin-bottom: 126px;
  padding: 0;
  font-family: DM Sans, sans-serif;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.user-icon {
  fill: var(--font-color-grey);
}

.mob-header-nav {
  justify-content: center;
  align-items: center;
  margin-bottom: 126px;
  display: flex;
}

.mob-menu-container {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.current-color {
  background-color: var(--font-color-yellow);
  border-radius: 36px;
}

.home-link {
  width: 72px;
  height: 34px;
  letter-spacing: -.04em;
  color: #111;
  text-transform: uppercase;
  align-items: center;
  padding: 8px 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.3;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.home-link:hover, .home-link:focus {
  transform: scale(.95);
}

.shoping-link {
  width: 118px;
  height: 20px;
  letter-spacing: -.04em;
  color: var(--font-color-white);
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.3;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.shoping-link:hover, .shoping-link:focus {
  transform: scale(.95);
}

.bag-icon {
  fill: var(--font-color-white);
}

.btn-logout {
  width: 118px;
  height: 48px;
  border: 1.5px solid var(--font-color);
  background-color: var(--font-color-white);
  letter-spacing: -.04em;
  cursor: pointer;
  z-index: 999;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  padding: 14px 24px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  position: absolute;
  bottom: 20px;
}

.arrow-right-icon {
  fill: var(--color-yellow);
}

.book-menu-img {
  width: 100%;
}

@media screen and (min-width: 0) and (max-width: 768px) {
  .header-nav {
    position: relative;
  }

  .mob-menu-container {
    position: absolute;
    top: 255px;
    left: 130px;
  }

  .shoping-link {
    margin-top: 14px;
  }

  .book-menu-img {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

@media screen and (min-width: 768px) {
  .header-container {
    padding-top: 14px;
    padding-bottom: 13px;
  }

  .logo-header {
    margin-right: 40px;
  }

  .header-nav {
    margin-right: 87px;
    display: block;
  }

  .mobile-menu, .menu-btn {
    display: none;
  }

  .menu-container {
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .current-color {
    background-color: var(--font-color-yellow);
    border-radius: 36px;
  }

  .shoping-link {
    width: 156px;
    height: 34px;
    color: var(--font-color);
