  }

  .block-btn {
    margin-right: 24px;
  }
}

@media screen and (min-width: 1440px) {
  .logo-header {
    margin-right: 76px;
  }

  .header-nav {
    margin-right: 0;
  }

  .menu-container {
    gap: 24px;
  }
}

@media screen and (min-width: 1440px) {
  .header-container {
    justify-content: space-between;
    display: flex;
  }
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.auth-logon {
  width: 184px;
  height: 48px;
  letter-spacing: -.56px;
  background-color: var(--color-violet);
  color: var(--font-color-white);
  border-color: #0000;
  border-radius: 18px;
  align-items: center;
  font-weight: 500;
  line-height: 1.28571;
  display: none;
}

.auth-logon-noneuser {
  width: 184px;
  justify-content: space-between;
  align-items: center;
  padding: 14px;
  display: flex;
}

.auth-logon-isuser {
  width: 184px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.auth-logon-isuser-text {
  color: var(--font-color-white);
  letter-spacing: -.44px;
  text-transform: capitalize;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.auth-logon .auth-logon-noneuser {
  padding: 14px;
}

.auth-logon .auth-logon-isuser {
  padding: 4px;
}

.auth-logout {
  width: 118px;
  height: 48px;
  border: 1.5px solid var(--border-color);
  background-color: var(--font-color-white);
  border-radius: 18px;
  align-items: center;
  padding: 15px 24px;
  display: flex;
}

.auth-logout-user {
  width: 118px;
  justify-content: space-between;
