}

.auth-logout-user-text {
  color: #111;
  letter-spacing: -.56px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.28571;
}

.backdrop-auth {
  width: 100vw;
  height: 100vh;
  background-color: var(--color-violet);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-auth.is-hidden .modal-auth {
  transform: translate(-50%, -50%)scale(0);
}

.modal-auth {
  width: 335px;
  height: 384px;
  background-color: var(--background-color1);
  border: 2px solid var(--border-color);
  border-radius: 18px;
  padding: 40px 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal-button-auth-close {
  width: 20px;
  height: 20px;
  border: none;
  outline: none;
  padding: 0;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-button-auth-close:focus .modal-button-auth-close-icon {
  stroke: var(--font-color-violet);
}

.modal-button-auth-close-icon {
  width: 20px;
  height: 20px;
  stroke: var(--font-color);
  background-color: var(--background-color1);
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-button-auth-close-icon:hover {
  stroke: var(--font-color-violet);
}

.modal-form-auth-field {
  flex-direction: column;
  display: flex;
}

.modal-form-auth-field-icon {
  width: 18px;
  height: 18px;
  stroke: var(--font-color);
  fill: var(--background-color1);
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
}

.modal-form-auth-field + .modal-form-auth-field {
  margin-top: 24px;
}

.modal-form-auth-field-input {
  width: 293px;
  height: 50px;
  border: 2px solid var(--border-color);
  color: var(--text-color-auth-form);
  background-color: var(--background-color-auth-form);
  letter-spacing: -.16px;
  text-transform: uppercase;
  border-radius: 40px;
  outline: #0000;
  align-items: center;
  gap: 10px;
  padding: 16px 50px 16px 24px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.125;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.modal-form-auth-field-input::placeholder {
  color: var(--text-color-auth-form);
  letter-spacing: -.16px;
  text-transform: uppercase;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.125;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-form-auth-field-inputhover .modal-form-auth-field-icon {
  stroke: var(--font-color-violet);
}

.modal-form-auth-field-input:is(hover, focus)::placeholder {
  color: var(--font-color-violet);
}

.modal-form-auth-field-input:is(hover, focus) {
  border-color: var(--font-color-violet);
}

.modal-form-auth .pos-authfocus-within .modal-form-auth-field-icon {
  stroke: var(--font-color-violet);
}

.modal-form-auth .pos-auth {
  position: relative;
}

.sign-buttons-auth {
  justify-content: center;
  gap: 20px;
  margin-top: 14px;
  display: flex;
}

.sign-button-auth {
  color: var(--text-buttons-submit);
  background-color: var(--background-color-auth-form);
  letter-spacing: -.14px;
  text-transform: uppercase;
  border: none;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28571;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.sign-button-auth:is(hover, focus) {
  color: var(--font-color-violet);
  outline: #0000;
  text-decoration-line: underline;
}

.sign-button-auth-submit {
  width: 294px;
  height: 50px;
  color: var(--text-color-submit);
  background-color: var(--background-color-submit);
  letter-spacing: -.16px;
